.artist {
    text-align: center;
    border-style: solid;
    border-width: 1px;
    height: 90vh;
  }
  
  .bio, .news {
    text-align: center;
    border-style: solid;
    border-width: 1px;
    height: 44.7vh;
    position: relative;
  }
  
  img {
    height: 500px;
  }
  
  .newsFeed, .bioinfo {
    display: inline-block;
    vertical-align: middle;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    width: 100%;
    height: 30%;
  }


